.about {
  padding-top: 62px;

  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;

    transition: all 0.5s;

    cursor: pointer;

    @include onTablets {
      font-size: 48px;
      padding-bottom: 0;
    }

    @include onDesktop {
      font-size: 64px;
      padding-bottom: 0;
    }
  }

  &__subtitle {
    padding-top: 32px;

    @include onTablets {
      padding-top: 0;
    }

    font-weight: 500;
    font-size: 23px;
    line-height: 140%;

    cursor: pointer;
  }
}
